main.listItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid rgba(0,0,0,.3);
  padding: .8em 1em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 3em;
}

.selected {
  font-size: 8px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 10px 0 5px;
  outline: none;
  cursor: pointer;
}

.notesTitle {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin-right: 5px;
  outline: none;
  cursor: pointer;
}

.delete {
  cursor: pointer;
}

.hover-delete .delete{
  display: none;
}

.hover-delete:hover .delete{
  display: block;
}



